html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Monstserrat", sans-serif;
  background-color: #ebeaea;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  stop-color: #001240;
  margin: 0;
  text-align: justify;
  color: #001240;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4 {
  margin: 0 0 5rem 0;
  cursor: default;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.5rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

h2 {
  font-size: 5.5rem;
  font-weight: 800;
}

.border {
  border-bottom: 0.5rem solid #d21314;
}

.text {
  display: flex;
  flex-direction: column;
  text-align: center;
  max-width: 90rem;
}

header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0.5% 0;
  background-color: #06065c;
}
header .logo {
  width: 8%;
}
header nav {
  display: flex;
  align-items: center;
  gap: 15%;
  margin-right: 5%;
}
header nav a {
  color: #ebeaea;
  font-size: large;
  height: 2.5rem;
  transition: all 0.4s;
}
header nav a:hover {
  border-bottom: 0.4rem solid #ebeaea;
}

.slide {
  position: relative;
  box-shadow: 0 7px 13px rgba(0, 0, 0, 0.5);
}
.slide .slide-inner {
  display: grid;
  grid-template-columns: 6% 88% 6%;
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 700px;
}
.slide .slide-inner .slide-open:checked + .slide-item {
  position: static;
  opacity: 100;
}
.slide .slide-inner .slide-item {
  grid-column: 2/3;
  position: absolute;
  opacity: 0;
  -webkit-transition: opacity 1.5s ease-in;
  transition: opacity 1.5s ease-in;
}
.slide .slide-inner .slide-item img {
  display: block;
  height: auto;
  position: absolute;
  width: 50%;
  left: 25%;
}
.slide .slide-inner .slide-control {
  background-color: #505050;
  color: #06065c;
  cursor: pointer;
  display: none;
  font-size: 40px;
  height: 40px;
  line-height: 35px;
  position: absolute;
  top: 50%;
  cursor: pointer;
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  text-align: center;
  width: 40px;
  z-index: 0;
  transition: all 1.5s;
}
.slide .slide-inner .slide-control:hover {
  background: radial-gradient(circle, rgba(158, 158, 158, 0.5) 0%, rgba(22, 22, 22, 0.5) 100%);
}
.slide .slide-inner .slide-control label {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.slide .slide-inner .slide-control.prev {
  grid-column: 1/2;
  width: 100%;
  height: 100%;
}
.slide .slide-inner .slide-control.next {
  grid-column: 3/4;
  width: 100%;
  height: 100%;
}
.slide .slide-inner #slide-1:checked ~ .control-1,
.slide .slide-inner #slide-2:checked ~ .control-2,
.slide .slide-inner #slide-3:checked ~ .control-3 {
  display: block;
}
.slide .slide-inner .slide-indicador {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  bottom: 2%;
  width: 20%;
  left: 40%;
  text-align: center;
  z-index: 0;
}
.slide .slide-inner .slide-indicador li {
  display: inline-block;
  margin: 0 5px;
}
.slide .slide-inner .slide-indicador .slide-circulo {
  color: #828282;
  cursor: pointer;
  display: block;
  font-size: 5rem;
  position: relative;
  top: 1.5rem;
}
.slide .slide-inner .slide-indicador .slide-circulo:hover {
  color: #aaaaaa;
}

#slide-1:checked ~ .control-1 ~ .slide-indicador li:nth-child(1) .slide-circulo,
#slide-2:checked ~ .control-2 ~ .slide-indicador li:nth-child(2) .slide-circulo,
#slide-3:checked ~ .control-3 ~ .slide-indicador li:nth-child(3) .slide-circulo {
  color: #ae0605;
}

.nosotros {
  display: flex;
  padding: 3% 10%;
  gap: 15%;
  align-items: center;
}
.nosotros .text {
  color: #06065c;
}
.nosotros .text h2 {
  padding-bottom: 1%;
  transition: all 1s;
}
.nosotros .text p {
  padding: 0 10%;
}
.nosotros picture {
  width: 100%;
}
.nosotros:hover .text h2 {
  border-bottom: 0.7rem solid #d21314;
}

.campos {
  background-color: #06065c;
  color: #ebeaea;
  display: flex;
  flex-direction: column;
  padding: 3% 10%;
}
.campos .campos-content,
.campos .campos-icon {
  display: flex;
  justify-content: center;
  gap: 8%;
  align-items: center;
}
.campos .campos-content .icon-card,
.campos .campos-icon .icon-card {
  background-color: #ebeaea;
  border-radius: 10%;
  text-align: center;
  padding: 1% 0;
  width: 100%;
  min-height: 100%;
  box-shadow: 3px 5px 5px 5px rgba(0, 0, 0, 0.5);
  transition: all 0.7s;
}
.campos .campos-content .hover-icon,
.campos .campos-icon .hover-icon {
  padding: 2%;
  box-shadow: 10px 15px 5px 5px rgba(0, 0, 0, 0.5);
  border: 0.5rem outset #d21314;
}
.campos .campos-content h4,
.campos .campos-icon h4 {
  color: #001240;
  margin: 5%;
}
.campos .campos-icon {
  margin: 0 auto;
  width: 80%;
  min-height: 25rem;
}
.campos p {
  color: #ebeaea;
  padding: 0 10%;
}

.vision {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.vision .vision-text {
  text-align: center;
  padding: 3rem 5rem;
  color: #06065c;
}
.vision .vision-text svg {
  position: relative;
  bottom: 5rem;
  right: 25rem;
}
.vision .bg-azul {
  background-color: #06065c;
  color: #ebeaea;
}
.vision .bg-azul p {
  color: #ebeaea;
}

footer {
  background-color: black;
}
footer .footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
footer .footer-content nav {
  display: flex;
  justify-content: center;
  gap: 2rem;
  border-bottom: 0.2rem solid #3a3939;
  padding: 1rem;
  width: 80%;
}
footer .footer-content nav a {
  color: #ebeaea;
  font-size: 2rem;
  height: 2.7rem;
  transition: all 0.4s;
}
footer .footer-content nav a:hover {
  border-bottom: 0.4rem solid #ebeaea;
}
footer .footer-content .logo {
  margin-top: 2rem;
  width: 6%;
}
footer .footer-content .border {
  border-bottom: #ebeaea;
}
footer .footer-info {
  width: 80%;
  padding: 0.5rem;
  display: grid;
  grid-template-columns: 33.3% 33.3% 33.3%;
  align-items: center;
  margin: 0 auto;
}
footer .footer-info .footer-text {
  margin: 0 auto;
}
footer .footer-info .footer-text p {
  color: #ebeaea;
  text-align: center;
  font-size: 1.5rem;
}
footer .footer-info h4 {
  color: #3a3939;
  margin: 0 auto;
  font-size: 1.5rem;
}
footer .footer-info .footer-redes {
  display: flex;
  gap: 1.5rem;
  margin: 0 auto;
  padding: 0.1rem;
}
footer .footer-info .footer-redes svg {
  background-color: #3a3939;
  border-radius: 50%;
  padding: 0.6rem;
  transition: all 0.8s;
}
footer .footer-info .footer-redes svg:hover {
  background-color: #ebeaea;
  stroke: #001240;
}